import { useNearContext } from "lib/near/near-context";
import { useWallet } from "lib/near/use-wallet";
import { usePayment } from "models/payment";
import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

export const useModel = () => {
  const location = useLocation();
  const app = useNearContext();
  const { payment = null, paymentId } = usePayment();
  const nearWallet = useWallet(payment);
  const navigate = useNavigate();

  useEffect(() => {
    if (payment == null) {
      return;
    }

    if (!app?.selector.isSignedIn()) {
      navigate(`/payment/${paymentId}`);
      return;
    }

    const token = new URLSearchParams(location.search).get("token");
    const currency = payment?.addresses.find((cur) => cur.token === token);
    nearWallet(currency ?? payment?.addresses[0]);
  }, [payment, app, paymentId, navigate, location, nearWallet]);
};
